import moment from 'moment';
import 'moment/locale/ko';
import './App.css';
import { useState } from 'react';

function App() {

  let post = '글 리스트';
  //state 는 변수안이 변경되면 자동으로 재랜더링됨
  let [글제목, 제목변경] = useState(['상품 추천','추천 상품','상품 설명']);
  let [글시간, 시간변경] = useState(['2020-08-23 12:54:30','2020-08-23 12:54:30','2020-08-23 12:54:30'])
  let [따봉,따봉변경] = useState([0,0,0]);
  let [modal,setmodal] = useState(false);
  let[입력값,입력값변경]=useState('');
//array 자료 갯수만큼 함수안 코드 실행해줌
//함수안의 파라미터는 array 안에 있던 자료임
//return 에 뭐 적으면 array에 담아줌
  [1,2,3].map(function(a){
    
  })

  // function likeit(i){
  //   let copy = [...따봉];
  //   copy[i] = copy[i] +1 ;
  //   따봉변경(copy);
  // }
  function 제목수정(){
    let copy = [...글제목];
    //state는 화살표료만 정의되서 독립적인 카피본 만들어서 수정해야함
    copy[0]='클린코드 작성법';
    제목변경(copy)
  }
  function 제목정렬(){
    let name = [...글제목];
    //state는 화살표료만 정의되서 독립적인 카피본 만들어서 수정해야함
    name = name.sort();
    제목변경(name);
  }
  function 상세창변경(){
    return(
      modal == false ? setmodal(true) : setmodal(false)
      //setmodal(!modal)
    )
  }

  return (
    <div className="App">
      <div className="black-nav">
      <h4 style={{color:'green', fontSize:'16px'}}>H's 블로그</h4>
      </div>
   
      <h4>{post}  <button onClick={제목수정}>글 수정</button><button onClick={제목정렬}>정렬 버튼</button></h4>    
    {/* <div className="list">
      <h4>{글제목[0]} <span onClick={likeit}> 👍🏻 </span>{따봉}</h4>
      <p>5월 15알 발행</p>
      </div>
    <div className="list">
      <h4>{글제목[1]} <span onClick={likeit}> 👍🏻 </span>{따봉}</h4>
      <p>5월 15알 발행</p>
      </div>
    <div className="list">
      <h4 onClick={상세창변경}>{글제목[2]} <span onClick={likeit}> 👍🏻 </span>{따봉}</h4>
      <p>5월 15알 발행</p>
      </div> */}
      
      {
      글제목.map(function(a,i){
        return(
          <div className="list">
        <h4 onClick={상세창변경}> {글제목[i]} 
        <span onClick={(e)=>{
          //상위 html로 퍼지는거 막고싶으면
            e.stopPropagation();
            let copy = [...따봉];
            copy[i] = copy[i] +1 ;
            따봉변경(copy);
        }}> 👍🏻 </span>{따봉[i]}</h4>
          <p>{글시간[i]}</p>
          <button onClick={()=>{
            let copy = [...글제목];
            //slice state 제거
            copy.splice(i,1);
            제목변경(copy)
          }}>삭제</button>
          </div>
        )
      })
    }
        
      
      {/* 동적인 UI만드는 step */}
      {/* 1 html 로 미리 디자인완성 */}
      {/* 2 ui 현재상태 state 저장*/}
      {/* 3 상때에 따른 보여줄 코드작성*/}
      {/* 모달의 조건식 삼항연상자 ternary operator */}
      {/* (참고1) props는 <Modal 이런거={이런거}  저런거={저런거}> 이렇게 10개 100개 1000개 무한히 전송이 가능합니다. */}
      {/* (참고2) 꼭 state만 전송할 수 있는건 아닙니다.  */}
      {/* <Modal 글제목={변수명}> 일반 변수, 함수 전송도 가능하고  */}
      {/* <Modal 글제목="강남우동맛집"> 일반 문자전송은 중괄호 없이 이렇게 해도 됩니다. */}
      {
        modal==true ? <상세페이지 color={'yellow'} 제목수정={제목수정} 글제목={글제목}/>:null 
      }
      
      <input onChange={(e)=>{입력값변경(e.target.value); console.log(입력값);}}/>
      <button onClick={()=>{
        if (입력값.length>0) {
          let copy = [...글제목];
          let copy1 = [...따봉];
          let copy2 = [...글시간];
          copy.unshift(입력값);
          copy1.unshift(0);
          copy2.unshift(moment().format('YYYY-MM-DD HH:mm:ss'));
          시간변경(copy2);
          따봉변경(copy1);
          제목변경(copy)
            } else{
              window.alert('빈창입니다.')
            }

        //글추가 unshift 하면됌
      }} >글 업로드</button>
    
      
    </div>
  );
}
//컴포넌트 만드는범(state 가져다 쓸때 문제점 생김)
// function만들고
//return() 안에 축약할 html담기
//함수명 /함수명 쓰기
//1. 쓰기좋은 유형 반복적인 html축약할때
//2. 큰 페이지들
//3. 자주 변경되는 것들

function 빈값(){
  return(

    <p>빈값</p>
  )
  
}
//props는 부모 자식간 교류만 가능 간섭이 안된다
function 상세페이지(props){
  return(
  <div className='modal' style={{background : props.color}}>
  <h4>{props.글제목[0]}</h4>
  <p>날짜</p>
  <p>상세내용</p>
  <button onClick={props.제목수정}>글수정</button>
</div>
 )
}

export default App;
